body {
  font: 22px "Century Gothic", sans-serif;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

p {  
  display: block;
  margin-block-start: 0.7em;
  margin-block-end: 0.7em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}

.status-info {
  margin-left: 20px;
  color: rgb(208,33,41);
}

.title {
  display: flex;
  flex-direction: column;
  margin-top: auto;
  margin-left: auto;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  color: rgb(208,33,41);
}

.button-container {
  margin-left: 1em;
}

.status {
  margin-bottom: 10px;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  width: 1400px;
  flex-direction: column;
  margin-top: 2.5em;
  justify-content: center;
  align-items: center;
  min-height: 15vh;
}

.scores-container {
  margin-right: 10em;
  color: rgb(208,33,41);
}

.create-button {
  margin-right: 20px;
  width: 10em;
  height: 5em;
  background-color: aqua;
  border-radius: 10%;
}

.join-button {
  width: 10em;
  height: 5em;
  background-color: rgb(255, 200, 47);
  border-radius: 10%;
}

.input-class{
  margin-top: -0.1em;
}

.title-class {
  font-size: 1.5rem; 
}

.popup-class{
  height: 165px; 
}

.join-button-class {
  margin: .3125em; 
  padding: .5em 1.8em; 
  margin-bottom: 1em; 
}

.button-class {
  margin: .3125em; 
  padding: .5em 1.8em; 
}

.control {
  position: absolute;
  top: 400px;
  left: 300px;
  width: 40em;
}

.pawn {
  position: absolute;
  transition: 1s ease-in-out;
  background: white;
}

#pawn1 {
  border: 2px solid red;
  left: 20px;
  top: 160px;
}

#pawn2 {
  border: 2px solid blue;
  left: 50px;
  top: 185px;
}

#pawn3 {
  border: 2px solid purple;
  left: 20px;
  top: 210px;
}

#pawn4 {
  border: 2px solid green;
  left: 50px;
  top: 235px;
}


.square {
	background: white;
  text-align: center;
  overflow: scroll;
}

.detail {
  width: 125px;
  visibility: hidden;
  position: absolute;
  display: block;
  position: absolute;
  border: 2px solid grey;
  background: lightsteelblue;
  z-index: 5;
}

.square:hover > .detail{
  visibility: visible;
  font-size: smaller;
}

.playersInfo {
  padding: 10px;
  margin: 5px;
}

.playersInfo:hover > .detail{
  visibility: visible;
}

.control > button {
  width: 12em;
  height: 2.0em;
  background-color: yellow;
  border-radius: 10%;
}

button:hover {
  cursor: pointer;
  border: 2px black solid;
}

button:disabled {
  cursor: auto;
  background-color: white;
}

button:disabled:hover {
  cursor: not-allowed;
  border: none;
  background-color: white;
}

.playersInfoWrapper {
  display: flex;
  text-align: center;
}

.playersInfo:nth-child(1) {
  border: 5px red solid;
}

.playersInfo:nth-child(2) {
  border: 5px blue solid;
}

.playersInfo:nth-child(3) {
  border: 5px purple solid;
}

.playersInfo:nth-child(4) {
  border: 5px green solid;
}

.chatArea {
  display: flex;
  flex-direction: column;
}

.chatInputArea > button {
  width: 12em;
  height: 2.0em;
  background-color: lightseagreen;
  border-radius: 10%;
}