

.board > div {
	display: grid;
	grid-template-columns: 125px repeat(9, 125px) 125px;
	grid-template-rows: 125px repeat(9, 125px) 125px;
	grid-gap: 2px;
	margin: auto;
	width: 1400px;
	height: 1400px;
	background: black;
	border: 2px solid black;
}

.center {
	grid-column: 2 / 11;
	grid-row: 2 / 11;
	background: white;
	display: grid;
	grid-template-columns: repeat(7, 1fr);
	grid-template-rows: repeat(7, 1fr);
	justify-items: center;
	align-items: center;
}

.row {
	display: grid;
	grid-gap: 2px;
}

.horizontal-row {
	grid-template-columns: repeat(9, 125px);
	grid-template-rows: 125px;
}

.vertical-row {
	grid-template-columns: 125px;
	grid-template-rows: repeat(9, 125px);
}

.bottom-row {
	grid-column: 2 / 11;
	grid-row: 11;
}

.left-row {
	grid-column: 1;
	grid-row: 2 / 11;
}

.top-row {
	grid-column: 2 / 11;
	grid-row: 1;
}

.right-row {
	grid-column: 11;
	grid-row: 2 / 11;
}

